*{box-sizing:border-box}body{margin:0;background-color:white}header{height:100px;display:flex;align-items:center;background-color:#F6FF00;justify-content:center}@media screen and (max-width: 630px){header{height:auto;padding:20px 20px;flex-direction:column;text-align:center}}header img{height:80%}@media screen and (max-width: 630px){header img{height:60px}}header h1{font-family:'Sniglet', cursive;font-weight:800;color:#2500FF;font-size:50px;margin-left:40px;text-transform:uppercase}@media screen and (max-width: 630px){header h1{font-size:3rem;margin:20px 0 0 0;line-height:47px}}

*{box-sizing:border-box}body{margin:0;background-color:white}.meme-wrapper{width:70%;margin:50px auto}@media screen and (max-width: 991px){.meme-wrapper{width:90%}}.meme-form{width:100%;margin:20px 0;display:flex;justify-content:center}@media screen and (max-width: 630px){.meme-form{flex-direction:column}}.meme-form input{width:100%;height:51px;font-family:'Sniglet', cursive;font-weight:400;font-size:20px;margin-right:20px;border-radius:12px;border:none;background-color:#2500FF;color:white;padding:10px 20px}@media screen and (max-width: 630px){.meme-form input{margin-bottom:10px}}.meme-form ::-webkit-input-placeholder{color:#cfd1fc}.meme-form :-ms-input-placeholder{color:#cfd1fc}.meme-form ::-ms-input-placeholder{color:#cfd1fc}.meme-form ::placeholder{color:#cfd1fc}.meme-form :-ms-input-placeholder{color:#cfd1fc}.meme-form ::-ms-input-placeholder{color:#cfd1fc}.meme-form button{border-radius:12px;padding:10px 20px;border:none;font-family:'Sniglet', cursive;font-weight:800;font-size:25px;text-transform:uppercase;letter-spacing:1.5px;color:#2500FF;background-color:#F6FF00}@media screen and (max-width: 630px){.meme-form button{width:70%;margin:10px auto 30px auto}}.meme{position:relative;width:100%;margin:auto}.meme img{width:100%}.meme h2{position:absolute;width:80%;text-align:center;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin:15px 0;padding:0 5px;font-family:impact, sans-serif;font-size:2em;text-transform:uppercase;color:white;letter-spacing:1px;text-shadow:2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000, 2px 2px 5px #000}.meme .bottom{bottom:0}.meme .top{top:0}

